<template>
  <div class="opportunities card">
    <div class="card-header">
      <h6 class="card-title mb-0">Сервисы</h6>
    </div>
    <div class="card-block">
      <ul class="pl-3 mb-0">
        <li v-for="service in data"><a :href="service.linkUrl">{{service.title}}</a></li>
      </ul>
    </div>
  </div>
</template>

<script>

  export default {
    props: {
      data: Array
    },
    computed: {}
  };

</script>

<style lang="scss">

  .opportunities {
    font-size: 1rem;
    padding-bottom: 10px;
    margin-bottom: 10px;

    ul {
      padding-top: 5px;
      list-style-type: none;
    }
  }

  .card-header {
    background-color: #D9F1FA;
  }

</style>



