<template>
  <div class="st-breadcrumbs-container">
    <ul class="st-breadcrumbs" v-if="data">
      <li class="st-breadcrumbs-item" v-for="breadcrumb in breadcrumbs">
        <a class="breadcrumb-link">{{breadcrumb.title}}</a>
        <span class="splitter">/</span>
      </li>
    </ul>
    <!--<span class="st-breadcrumbs-item current col-4">-->
      <!--<span class="splitter">/</span>-->
      <!--<a class="breadcrumb-link">{{data[ data.length - 1 ].title}}</a>-->
    <!--</span>-->
  </div>
</template>

<script>

  export default {
    props: {
      data: Array
    },
    computed: {
      breadcrumbs: function () {
        return this.data; //.slice(0,-1);
      }
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors";

  .st-breadcrumbs {
    list-style-type: none;
    padding: 10px 0;
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .st-breadcrumbs-item {
    display: inline-block;
    padding-right: 5px;
    color: $c-muted;

    &:last-child {
      color: $c-primary;

      .splitter {
        display: none;
      }
    }

    &.current {
      color: $c-primary;
    }
  }

  .st-breadcrumbs-item:last-child .fa {
    display: none;
  }

  .splitter {
    color: #26628A;
    font-weight: 600;
  }

  .breadcrumb-main-section {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-top: -5px;
    line-height: 16px;
    display: inline-block;
  }
</style>



