<template>
  <div class="st-contacts">
    <div class="mb-2">
      <i class="fa fa-phone fa-2x align-middle" aria-hidden="true"></i>
      <h6 class="info-title">Контактная информация</h6>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Телефон:</h6>
        </div>
      </div>
      <div class="row" v-for="phone in data.phones">
        <div class="col-4">{{phone.type}}:</div>
        <div class="col-8">{{phone.number}}</div>
      </div>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Электронная почта:</h6>
        </div>
      </div>
      <div class="row" v-for="mail in data.emails">
        <div class="col-12"><a :href="'mailto:' + mail.address">{{mail.address}}</a> ({{mail.description}})</div>
      </div>
    </div>
    <div class="section">
      <div class="row">
        <div class="col">
          <h6 class="section-title">Вебсайт:</h6>
        </div>
      </div>
      <div class="row" v-for="site in data.sites">
        <div class="col-12"><a :href="site.link">{{site.link}}</a> ({{site.description}})</div>
      </div>
    </div>
    <div class="section" v-show="data.freeText"><span v-html="data.freeText"></span></div>
  </div>

</template>

<script>
  export default {
    props: {
      data: Object
    },
    computed: {}
  };
</script>

<style>
  .section-title {
    padding-top: 5px;
    margin-bottom: 0;
  }
</style>



