<template>
  <nav class="main-navbar sticky-top">
    <div class="container-fluid">
      <a href="#/home" class="logo home-link">
        <div class="logo-title">Вакол</div>
        <div class="logo-sub-title">Стежкамi роднай краiны</div>
      </a>
      <div class="menu">
        <div class="menu-item">Места
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
        <div class="menu-item">Маршруты
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
        <div class="menu-item">О нас
          <div class="sub-items">
            <div class="sub-item">First</div>
            <div class="sub-item">Second</div>
          </div>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>

  export default {
    name: 'navbar'
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import "../styles/variables";
  @import "../styles/colors";

  .main-navbar {
    height: 60px;
    background-color: $c-primary;
    color: $c-white;
  }

  .logo {
    float: left;
    padding-top: 10px;
    display: block;
    color: $c-white;
    text-decoration: none;
  }

  .logo-title {
    line-height: 1;
    font-size: 1.6rem;
  }

  .logo-sub-title {
    font-size: 0.7rem;
  }

  .menu {
    float: right;
    padding-top: 20px;
  }

  .menu-item {
    text-transform: uppercase;
    border-left: solid 1px $c-white;
    border-right: solid 1px $c-white;
    padding: 0 5px;
    font-size: 0.7rem;
    display: inline-block;
    position: relative;

    @media (min-width: 500px) {
      padding: 0 15px;
      font-size: 0.8rem;
    }
  }

  .menu-item:first-child {
    border: none;
  }

  .menu-item:last-child {
    border: none;
  }

  .sub-items {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $c-light;
    width: 150px;
  }

  .menu-item:hover .sub-item {
    display: block;
    color: #f11e1c;
  }

  .container-fluid {
    max-width: $container-max-width;
  }
</style>



