<template>
  <div class="st-what-to-take">
  	<i class="fa fa-suitcase fa-2x align-middle" aria-hidden="true"></i>
  	<h6 class="info-title">Что взять с собой</h6>
  	<div class="row">
  		<div class="col-4" v-for="item in data">
  			<i class="fa fa-check" aria-hidden="true"></i> {{item.title}}
  		</div>
  	</div>
  </div>
</template>

<script>
  export default {
    data: function () {
      return {};
    },
    props: {
      data: Array
    },
    computed: {}
  };
</script>

<style>

</style>



