<template>
  <div class="searchbox">
    <div class="row">
      <b-form @submit="onSubmit" class="col-12">
        <div class="row">
          <b-form-group label-for="albumName" label="Начальная точка:" class="col-12 col-sm-6 col-lg-3">
            <b-form-input id="startingPoint"
                          name="startingPoint"
                          type="text" v-model="form.startingPoint"
                          placeholder="Введите имя начальной точки"
            ></b-form-input>
          </b-form-group>
          <b-form-group label-for="albumName" label="Конечная точка:" class="col-12 col-sm-6 col-lg-3">
            <b-form-input id="endingPoint"
                          name="endingPoint"
                          type="text" v-model="form.endingPoint"
                          placeholder="Введите имя конечной точки"
            ></b-form-input>
          </b-form-group>
          <b-form-group label-for="albumName" label="Продолжительность:" class="col-12 col-sm-6 col-lg-3">
            <b-form-select  id="duration"
                            name="duration"
                            v-model="form.duration"
                            :options="hoursOptions" class="mb-3">
            </b-form-select>
          </b-form-group>
          <b-form-group label-for="albumName" label="Вид транспорта:" class="col-12 col-sm-6 col-lg-3">
            <b-form-select  id="transportationType"
                            name="transportationType"
                            v-model="form.transportationType"
                            :options="transportationOptions" class="mb-3">
            </b-form-select>
          </b-form-group>
        </div>
        <div class="row">
          <div class="col"><a href="">Расширенный поиск</a></div>
          <div class="col">
            <button type="submit" class="btn btn-primary pull-right">Смотреть варианты</button>
          </div>
        </div>
      </b-form>
    </div>
    <div class="row">

    </div>
  </div>
</template>

<script>

  export default {
    computed: {},
    data() {
      return {
        form: {
          startingPoint: '',
          endingPoint: '',
          duration: 8,
          transportationType: null
        },
        transportationOptions: [
          { value: null, text: 'Выберите тип транспорта' },
          { value: 'car', text: 'Автомобиль' },
          { value: 'atbs', text: 'Автобус' },
          { value: 'skate', text: 'Самокат' }
        ],
        hoursOptions: [
          { value: 4, text: '4 часа' },
          { value: 6, text: '6 часов' },
          { value: 8, text: '8 часов' },
          { value: 12, text: '12 часов' }
        ]
      };
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault();
        console.log('Searched');
        this.$router.push('search-results');
//        if (!this.isEditMode) {
//          this.$store.dispatch('createNewAlbum', Object.assign(this.form, this.$store.state.uploadedImages)).then(() => {
//            this.$store.dispatch('resetUploadedImages');
//            this.$router.push('/home');
//          });
//        } else {
//          this.$store.dispatch('updateAlbum', Object.assign(this.form, this.$store.state.uploadedImages)).then(() => {
//            this.$store.dispatch('resetUploadedImages');
//            this.$router.push('/home');
//          });
//        }
      }
    }
  };

</script>

<style lang="scss">
  @import "../styles/colors.scss";

  .searchbox {
    background-color: $c-humming-bird;
    border-radius: 6px;
    padding: 10px;
    margin-top: 10px;
  }

</style>



